.gpt3__header {
    display: flex;
}
.gpt3__header-container {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin: 0 5rem;     
}
.gpt3__header-container h1 {
    font-family:var(--font-family) ;
    font-size: 62px;
    font-style: normal;
    font-weight: 800;
    line-height: 75px; 
    letter-spacing: -2.48px;
}
.gpt3__header-container p {
    color: var(--color-text);
    font-family:var(--font-family);
    font-size: 20px;
    font-style: normal;
    font-weight: normal;
    line-height: 27px;

}
.dpt3__header-content_input {
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
}
.dpt3__header-content_input input {
    flex: 2;
    width: 100%;
    min-height:50px;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    background: var(--color-footer);
    border: 2 solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.dpt3__header-content_input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    background: #ff4820;
    color: #fff;
    cursor: pointer;
    padding: 0 1rem;
    border:none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;

}
.gpt3__header-container__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 2rem 0;
}
.gpt3__header-container__people img {
    width: 181.79px;
    height: 38px;
}
.gpt3__header-container__people p {
    margin-left: 1rem;
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    font-style: normal;
    line-height: 38px;
    color: #fff;
}
.gpt3__header-image {
    flex:1 ;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gpt3__header-image img {
    width: 100%;
    height: 100%;
}
@media all and (max-width:1050px) {
    .gpt3__header {
        flex-direction: column;
    }
    .gpt3__header-content {
        margin: 0 0 3rem;
    }
}
@media all and (max-width:650px) {
    .gpt3__header h1 {
        font-size: 48px;
        line-height :60px; ;
    }
    .gpt3__header p {
        font-size: 16px;
        line-height: 24px;
    }
    .gpt3__header-container__people {
        flex-direction: column;
    }
    .gpt3__header-container__people p {
        margin: 0;
    }
    .dpt3__header-content_input input,
    .dpt3__header-content_input button {
        font-size: 16px;
        line-height: 24px;
    }
}  
@media all and (max-width:490px) {
    .gpt3__header h1 {
        font-size: 36px;
        line-height :48px; ;
    }
    .gpt3__header p {
        font-size: 14px;
        line-height: 24px;
    }
    
    .dpt3__header-content_input input,
    .dpt3__header-content_input button {
        font-size: 12px;
        line-height: 16px;
    }
}  