.gpt3__possibility {
    display: flex;
    flex-direction: row;
}
.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;
}
.gpt3__possibility-image img{
    width: 100%;
    height: 100%;
}
.gpt3__features-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}
.gpt3__features-content h4 {
    font-family: var(--font-family);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px; 
    color: #71E5FF;
    margin-bottom: 2rem;
}
.gpt3__features-content h1 {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 34px;
    font-style: normal;
    font-weight: 800;
    line-height: 45px; 
    margin: 1rem 0;
}
.gpt3__features-content p {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text); 
    margin-bottom: 2rem; 
}
.gpt3__features-content h4:last-child {
    color: #FF8A71;
}
@media all and (max-width:950px) {
    .gpt3__possibility {
        flex-direction: column;
    }
    .gpt3__possibility-image {
        margin: 1rem 0;
    }
    .gpt3__features-content {
        margin: 1.99rem 0;  
    }
}